.errorDiv {
  width: 100%;
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
}
.errorImg {
  margin: 2rem 0 0 0;
  width: 5rem;
  height: auto;
}
.cowHome {
  margin-top: 1rem;
  width: 2rem;
}

@media screen and (min-width: 48rem) {
  .errorDiv {
    height: 64vh;
  }
  .errorImg {
    width: 15rem;
    height: auto;
  }
  .cowHome {
    width: 5rem;
    height: auto;
  }
  .errorDiv p {
    font-size: 2rem;
  }
}
