.btnProduct {
  height: 2.25rem;
  width: max-content;
  margin: 0 0 1rem 1rem;
  text-align: center;
  background-color: var(--blue-100);
  border: thin solid var(--blue-100);
  color: #fff;
}
.center {
}
.allProducts {
  margin: 0 auto;
  width: 100%;
}
.flexTitleAll {
  padding: 0 0 3rem 0;
}
.flexTitleAll h2 {
  margin: 0;
  padding: 1rem;
}
.allProducts .card {
  display: block;
  margin: 0 auto;
}

@media screen and (min-width: 48rem) {
  .center {
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .allProducts {
    width: 95%;
    margin: 0 auto;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: baseline;
  }
  .allProducts .card {
    margin: 0;
  }
}
@media screen and (min-width: 64rem) {
  .sectionTitle {
    font-size: 1.75rem;
  }
  .flexTitleAll {
    width: 80vw;
    margin: 0 auto;
  }
  .btnProduct {
    height: 3rem;
    width: 10rem;
    font-size: 1.15rem;
  }
}
