:root {
  --blue-100: #2a7ae4;
  --gray: #f5f5f5;
  --light-blue: #eaf2fd;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;

  scrollbar-width: thin;
  scrollbar-color: var(--blue-100) aliceblue;
}

body {
  margin: 0px;
  background-color: #fff;
  font-family: "Raleway", sans-serif;
}
.space {
  margin: 1rem 0 1.5rem 0;
}
.background {
  background-color: var(--gray);
}

.logo {
  height: 1.75rem;
  width: auto;
}

.flexTitle {
  height: 2rem;
  padding: 1.5rem 1rem;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
}

.sectionTitle {
  font-size: 1.25rem;
  padding: 1rem 0;
}
.link {
  display: flex;
  justify-content: baseline;
}

.btn {
  height: 2.25rem;
  width: 6.5rem;
  margin: 0 0 1rem 1rem;
  text-align: center;
  background-color: var(--blue-100);
  border: thin solid var(--blue-100);
  color: #fff;
  border-radius: 4px;
}

.viewAll {
  display: flex;
  justify-content: baseline;
  align-items: baseline;
}

.viewAll img {
  margin-left: 0.25rem;
  height: 1rem;
  width: 1rem;
}

.grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: baseline;
  margin: 0 auto;
}
.grid .card {
  margin: 0 auto;
}

.input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.25rem 1rem;
}
.inputLabel {
  background-color: white;
  color: #00000075;
  padding: 0.25rem;
  font-size: 0.75rem;
}

.h3Product {
  margin: 0;
  padding: 1rem;
}
.consoles {
  margin: 0 auto;
  padding: 0 0 12rem 0;
  height: max-content;
}

@media screen and (min-width: 48rem) {
  .sectionTitle {
    margin-top: 1rem;
  }
  .grid {
    flex-wrap: nowrap;
    justify-content: space-around;
  }
  .grid .card {
    margin: 0;
  }
  .consoles .grid {
    flex-wrap: wrap;
    justify-content: start;
  }
  .consoles .grid .card {
    margin: 0;
  }
}
@media screen and (min-width: 64rem) {
  .group {
    width: 80vw;
    margin: 0 auto;
    padding: 1rem 0 2rem 0;
  }
  .consoles .grid {
    justify-content: flex-start;
    align-items: flex-start;
  }
  .consoles .grid .card {
    margin: 0;
  }
}
