.formProduct {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.inputProduct {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.25rem 1rem;
  width: 100%;
  margin: 0.15rem;
}

.select {
  background: white;
  border: none;
  border-bottom: #00000075;
}
.inputProduct option {
  font-size: 1.25rem;
}
.textArea {
  resize: none;
  width: 100%;
  outline: none;
}
.btnNewProduct {
  width: 40%;
  height: 2.25rem;
  text-align: center;
  background-color: var(--blue-100);
  border: thin solid var(--blue-100);
  color: #fff;
  margin: 1rem;
}
@media screen and (min-width: 48rem) {
  .heigthForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 64vh;
  }
  .h3Product {
    font-size: 1.5rem;
    width: 80vw;
    margin: 0 auto;
  }

  .formProduct {
    width: 90vw;
    margin: 0 auto;
  }
}
@media screen and (min-width: 64rem) {
  .heigthForm {
    width: 80vw;
    height: 73vh;
    margin: 0 auto;
  }
  .formProduct {
    width: 100%;
  }
  .inputLabel,
  .inputField,
  select {
    font-size: 1.15rem;
  }
}
