.navbar {
  position: sticky;
  z-index: 999;
  width: 100%;
  top: 0;
  background-color: #ffffff;
}
.header {
  display: flex;
  width: 100%;
  height: 3rem;
}
.flex {
  padding: 0.25rem 0.5rem;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.linkLogo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.loginBtn {
  width: 8rem;
  height: 2.5rem;
  background-color: inherit;
  border: thin solid var(--blue-100);
  color: var(--blue-100);
  font-weight: 500;
  border-radius: 4px;
}
.icone {
  border: none;
  background-color: inherit;
}
.icone img {
  width: 1.5rem;
  height: 1.5rem;
}

.visible {
  display: auto;
}
.invisible {
  display: none;
}
.mobile {
  display: none;
}
.show {
  visibility: hidden;
}

@media screen and (min-width: 48rem) {
  .mobile {
    margin: auto;
    display: block;
    align-self: stretch;
  }
  .logo {
    height: inherit;
    width: auto;
  }
  .loginBtn {
    height: 2.5rem;
    font-size: 1.15rem;
  }
  .icone {
    display: none;
  }
}
@media screen and (min-width: 64rem) {
  .header {
    width: 80vw;
    margin: 0 auto;
  }
  .header {
    height: 3.5rem;
  }
  .logo {
    height: inherit;
    width: auto;
  }
  .loginBtn {
    width: 8rem;
    height: 3rem;
    font-size: 1.5rem;
    font-weight: 500;
  }
  .flex {
    justify-content: baseline;
  }
}
