.footer {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.bg-footer {
  background-color: var(--light-blue);
}
.footer .logo {
  margin: 1rem auto;
  height: 3rem;
}
.navlinks {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}
.navlinks a {
  text-decoration: none;
  padding: 0.5rem 0;
  color: #00000075;
}
.navlinks a:visited {
  color: #00000075;
}
.cardForm p {
  padding: 0 1rem;
  margin: 0 auto;
}

.inputField {
  border: none;
  border-bottom: thin solid #00000075;
  outline: none;
}

.byme {
  background-color: var(--blue-100);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  width: 100%;
  height: 3rem;
  text-align: center;
}
.byme a {
  text-decoration: none;
}
.byme a:visited {
  color: white;
}
.father {
  position: relative;
}

.messageSend {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--light-blue);
  border: 0.5rem solid var(--blue-100);
  width: 10rem;
  height: 4rem;
  border-radius: 4rem;
}
.messageSend p {
  color: var(--blue-100);
  text-align: center;
}

@media screen and (min-width: 48rem) {
  .logoFooter {
    grid-area: logoFooter;
  }
  .father {
    grid-area: father;
    margin-top: 1rem;
  }
  .father p {
    font-weight: 600;
    padding: 1rem;
  }
  .father .input {
    margin-right: 2rem;
  }
  .navlinks {
    grid-area: navlinks;
  }

  .father input[type="text"] {
    height: 2rem;
    font-size: 1.25rem;
  }
  .father label {
    height: 2rem;
    font-size: 1.25rem;
  }
  .father input[type="submit"] {
    margin-top: 0.5rem;
    height: 2.75rem;
    width: 10rem;
    font-size: 1.25rem;
  }
  .footer {
    height: 40%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: (2, auto);
    grid-template-areas:
      "logoFooter father father"
      "navlinks father father"
      "navlinks father father";
  }
}

@media screen and (min-width: 64rem) {
  .footer {
    width: 80vw;
    margin: 0 auto;
    height: 18rem;
    display: grid;
    grid-template-columns: 1fr 45% 45%;
    grid-template-rows: (2, auto);
    grid-template-areas:
      "logoFooter navlinks father"
      "logoFooter navlinks father"
      "logoFooter navlinks father";
  }
}
