.search {
  width: 100%;
  height: auto;
  padding: 0.5rem 0;
}
.searchBar {
  width: 100%;
  height: auto;
}

.searchInputs {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
}

.search input {
  background-color: white;
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 1.15rem;
  width: 100%;
  height: inherit;
}

.searchIcon {
  height: 1.5rem;
  padding: 0 0.5rem;
  width: max-content;
  background-color: white;
  color: gray;
}

input:focus {
  outline: none;
}

.searchIcon svg {
  width: 1.5rem;
  height: 1.5rem;
}

.dataResult {
  position: relative;
  z-index: 999;
  width: 100%;
  margin: 0 auto;
  height: max-content;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
  border-radius: 0;
  border-bottom-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
}

.dataResult::-webkit-scrollbar {
  display: none;
}

.dataResult .dataItem {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: black;
  border-bottom: thin solid #00000075;
}

.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: lightgrey;
}
a:visited {
  background-color: lightgrey;
}

#clearBtn {
  cursor: pointer;
}
.nameProduct {
  padding: 0.25rem 0.5rem;
  margin: 0;
}

@media screen and (min-width: 48rem) {
  .mobile {
    display: block;
  }
  .search {
    width: 20rem;
    height: auto;
  }
  .searchInputs {
    width: 20rem;
    background-color: #f5f5f5;
    border: none;
    padding: 0.00000001rem;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  .dataResult {
    background-color: #f5f5f5;
    box-shadow: none;
    border-top: thin solid #00000075;
  }
  .dataItem {
    background-color: #f5f5f530;
  }
  .search input {
    background-color: transparent;
    border: 0;
  }
  .searchIcon {
    background-color: transparent;
    border-left: thin solid #00000075;
  }
}
@media screen and (min-width: 64rem) {
  .search {
    width: 30rem;
  }
  .searchInputs {
    width: 30rem;
  }
}
