.heroFlex {
  width: 100%;
  height: 12rem;
}
.heroImg {
  background-image: url(https://raw.githubusercontent.com/Jooelg/E-CommerceImg/20162fa7c47f59c9ec6450dae94b0d9d7a737807/ProductImg/hero.svg);
  background-position: center;
  background-size: cover;
}
.heroText {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  width: 100%;
  height: inherit;
}
.imgText {
  color: #fff;
  margin: 0.25rem;
  margin: 0 0 0.5rem 1rem;
}
@media screen and (min-width: 48rem) {
  .heroImg {
    height: 18rem;
  }
  .heroText h2 {
    font-size: 2.5rem;
  }
  .heroText p {
    font-size: 1.25rem;
  }
  .btnConsole {
    height: 3rem;
    font-size: 1.15rem;
  }
}
@media screen and (min-width: 64rem) {
  .heroImg {
    height: 20rem;
  }
  .heroText {
    padding-bottom: 1rem;
    width: 80vw;
    margin: 0 auto;
  }
  .heroText h2 {
    font-size: 3.5rem;
  }
  .heroText p {
    font-size: 1.75rem;
  }
  .btnConsole {
    width: 8rem;
    height: 3rem;
    font-size: 1.25rem;
  }
}
