.card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 50vw;
  min-width: 9.75rem;
  height: auto;
  padding: 0.5rem;
  position: relative;
}
.card:nth-of-type(n + 5) {
  display: none;
}
.imgCard {
  width: 100%;
  height: auto;
  min-height: 11rem;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
}
.card p {
  margin: 0.25rem 0;
}
.link {
  text-decoration: none;
  color: var(--blue-100);
  font-weight: bold;
}
.editDelete {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.btnED {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.5rem;
  display: inline;
}
.btnED img {
  width: 1rem;
  height: 1rem;
}
.editDelete img {
  width: 1rem;
  height: 1rem;
}
@media screen and (min-width: 48rem) {
  .card {
    min-width: 12rem;
  }
  .imgCard {
    width: 100%;
    height: auto;
    min-height: 12rem;
  }
}
@media screen and (min-width: 64rem) {
  .card {
    min-width: 16rem;
  }
  .imgCard {
    min-height: 16rem;
  }

  .card:nth-of-type(n - 7) {
    display: block;
  }
  .card:nth-of-type(n + 7) {
    display: none;
  }
}
