.flexCard {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.productView {
  margin: 0 auto;
  position: relative;
}
.similarProducts {
  height: max-content;
  width: 100%;
  margin: 0 0 3rem 0;
}
.similarh4 {
  padding: 0 1rem;
}
.productView img {
  width: 100%;
  height: auto;
}
.productDetails {
  padding: 0 1rem;
}
.editDelete {
  background-color: transparent;
  border: none;
  cursor: pointer;
  height: 2rem;
  width: auto;
}
.editDelete img {
  height: 2rem;
  width: auto;
}
.btnED {
  position: absolute;
  top: 0;
  right: 0;
  display: inline-block;
}
.flexCard .card {
  margin: 0 auto;
}

@media screen and (min-width: 48rem) {
  .flexCard .card {
    margin: 0;
  }
  .productFlex {
    padding: 1rem;
    height: max-content;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .productView {
    width: 50%;
  }

  .productDetails {
    width: 50%;
  }

  .similarProducts {
    width: 100%;
    overflow-x: scroll;
  }
  .flexCard {
    width: max-content;
    flex-wrap: nowrap;
  }

  .flexCard .card {
    display: block;
  }
  .btnED {
    width: 100%;
    height: max-content;
  }
  .editDelete img {
    height: 3rem;
    width: auto;
  }
}
@media screen and (min-width: 64rem) {
  .productFlex {
    margin: 0 auto;
    padding: 5rem 0;
    display: flex;
    flex-direction: row;
    justify-content: baseline;
    align-items: center;
  }
  .similarh4 {
    margin: 0;
    margin-bottom: 1rem;
    font-size: 1.75rem;
    padding: 0;
  }
  .productDetails {
    width: 70%;
    padding: 0 5rem;
  }
  .productDetails h2 {
    font-size: 2rem;
  }
  .productDetails h5 {
    font-size: 1.25rem;
  }
  .editDelete img {
    height: 3rem;
    width: auto;
  }
}
