.login {
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 1rem;
}
.inputField2 {
  border: none;
  border-bottom: thin solid #00000075;
  outline: none;
  margin-bottom: 1rem;
  width: 90%;
  height: 2rem;
}

@media screen and (min-width: 48rem) {
  .inputField2 {
    width: 25rem;
    height: 3rem;
    font-size: 1.25rem;
  }
  .login .btn {
    width: 10rem;
    height: 3rem;
    font-size: 1.25rem;
  }
  .login {
    height: 64vh;
  }
}
@media screen and (min-width: 64rem) {
  .formBox {
    height: 63vh;
  }
}
